.input{
	width: 100%;
	padding: 12px;
	margin: 10px 2px;
	border-radius: 20px;
	box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
}

.input:invalid{
	border: 2px solid rgba(245, 3, 3, 0.734);
	box-shadow: 0 0 5px 5px rgb(245, 3, 3, 0.734);
}

.input:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}