.authForm{
	padding: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.formTable{
	width: 600px;
	height: 350px;
	padding: 40px;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background: rgb(243, 241, 241);
	box-shadow: 5px 5px 5px 5px #000000c7;
}

.error{
	color: rgb(248, 8, 8);
}

form{
	margin: 10px;
}

.link{
	color: rgb(161, 5, 252);
}

.link:hover{
	text-decoration: underline;
}

.registLink{
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.notActive{
	display: flex;
	justify-content: center;
	align-items: center;
}